import React, { useEffect } from 'react';
import { Link as RouterLink, useNavigate, useParams } from 'react-router-dom';
// @mui
import { styled } from '@mui/material/styles';
import { Box, Button, Container, Typography } from '@mui/material';
import { useSnackbar } from 'notistack';
// layouts
import LogoOnlyLayout from '../../layouts/LogoOnlyLayout';
// routes
import { PATH_AUTH } from '../../routes/paths';
// components
import Page from '../../components/Page';
import Iconify from '../../components/Iconify';
// sections
import useAuth from '../../hooks/useAuth';

// ----------------------------------------------------------------------

const RootStyle = styled('div')(({ theme }) => ({
  display: 'flex',
  height: '100%',
  alignItems: 'center',
  padding: theme.spacing(12, 0),
}));

// ----------------------------------------------------------------------

export default function ForgotVerify() {
  const { forgotVerify } = useAuth();
  const navigate = useNavigate();
  const { token } = useParams();
  const { enqueueSnackbar } = useSnackbar();

  useEffect(() => {
    onVerify(token);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [token]);

  const onVerify = async (token) => {
    try {
      await forgotVerify(token);
      enqueueSnackbar('Verify success!');
      navigate(PATH_AUTH.newPassword, { state: { varificationCode: token } });
    } catch (error) {
      if (error.errors) {
        error.errors.map((e) =>
          enqueueSnackbar(e, {
            variant: 'error',
          })
        );
      }
      navigate(PATH_AUTH.resetPassword, { replace: true });
    }
  };

  return (
    <Page title="Verify" sx={{ height: 1 }}>
      <RootStyle>
        <LogoOnlyLayout />

        <Container>
          <Box sx={{ maxWidth: 480, mx: 'auto' }}>
            <Button
              size="small"
              component={RouterLink}
              to={PATH_AUTH.resetPassword}
              startIcon={<Iconify icon={'eva:arrow-ios-back-fill'} width={20} height={20} />}
              sx={{ mb: 3 }}
            >
              Back
            </Button>

            <Typography variant="h3" paragraph>
              Verifcation is Process
            </Typography>
            <Typography sx={{ color: 'text.secondary' }}>
              please wait...
            </Typography>
          </Box>
        </Container>
      </RootStyle>
    </Page>
  );
}
